.testimonials{
    display: flex;
    gap: 1rem;
    padding: 0 2rem;
}
.testimonial-left{
    display: flex;
    flex: 1 1;
    gap: 2rem;
    flex-direction: column;
    text-transform: uppercase;
    color: white;
}

.testimonial-left>:nth-child(1){
    color: var(--orange);
    font-weight: bold;
}
.testimonial-left>:nth-child(2),
.testimonial-left>:nth-child(3){
    font-size: 3rem;
    font-weight: bold;
}
.testimonial-left>:nth-child(4){
    text-transform: none;
    text-align: justify;
    line-height: 40px;
    letter-spacing: 2px;
}

.testimonial-right{
    flex: 1;
    position: relative;
}
.testimonial-right>img{
    position: absolute;
    width: 17rem;
    height: 20rem;
    object-fit: cover;
    right: 8rem;
    top: 2rem;
}
.testimonial-right>:nth-child(1){
    position: absolute;
    width: 17rem;
    height: 20rem;
    border: 2px solid orange;
    background-color: transparent;
    right: 9rem;
    top: 0.9rem;
}
.testimonial-right>:nth-child(2){
    position: absolute;
    width: 17rem;
    height: 20rem;
    background: var(--planCard);
    right: 7rem;
    top: 4rem;
}
.arrows{
    display: flex;
    gap: 1rem;
    position: absolute;
    bottom: 1rem;
    left: 3rem;
}
.arrows>img{
    width: 1.5rem;
    cursor: pointer;
}

@media screen and (max-width:768px) {
    .testimonials{
        flex-direction: column;
    }
    .testimonial-left>:nth-child(2),
    .testimonial-left>:nth-child(3) {
        font-size: xx-large;
    }
    .testimonial-right{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 2rem;
    }
    .testimonial-right>div{
        position: relative;
        display: none;
    }
    .testimonial-right>img{
        top: 0;
        right: 0;
        position: relative;
        align-self: center;
    }
    .testimonial-right > :last-child{
        display: flex;
        bottom: 0;
        left: 0;
    }
    
}